import * as THREE from 'three'
import Experience from './Experience.js'

export default class Dash {
    constructor() {
        this.experience = new Experience()
        this.scene = this.experience.scene
        this.time = this.experience.time
        this.sizes = this.experience.sizes
        this.mouse = this.experience.mouse
        this.resources = this.experience.resources
        this.world = this.experience.world

        this.setGeometry()
        this.setTextures()
        this.setMaterial()
        this.setMesh()
    }

    setGeometry() {
        this.geometry = new THREE.PlaneGeometry(1, 1, 1, 1)
    }

    setTextures() {
        this.textures = {}
        this.textures.color = this.resources.items.dashTexture
        this.textures.encoding = THREE.sRGBEncoding
    }

    setMaterial() {
        this.material = new THREE.MeshStandardMaterial({
            depthWrite: false,
            transparent: true,
            map: this.textures.color
        })
    }

    setMesh() {
        this.mesh = new THREE.Mesh(this.geometry, this.material)
        this.mesh.position.x = 0.25
        this.mesh.rotation.z = 0.25
        this.scene.add(this.mesh)
    }

    update() {

        this.mesh.position.z = 1 + Math.sin(this.time.elapsed * 0.001) * 0.1
        this.mesh.rotation.y = Math.sin(this.time.elapsed * 0.001) * 0.2
        this.mesh.position.x = 0.25 + (this.mouse.scroll / this.sizes.height * 0.25)
        this.mesh.rotation.z = 0.25 + (-this.mouse.scroll / this.sizes.height * 0.25)
    }

}