import * as THREE from 'three'
import Experience from './Experience.js'
import vertexShader from './Shaders/Vignette/vertex.glsl'
import fragmentShader from './Shaders/Vignette/fragment.glsl'

export default class Vignette {
    constructor() {
        this.experience = new Experience()
        this.scene = this.experience.scene
        this.time = this.experience.time

        this.setGeometry()
        this.setColor()
        this.setMaterial()
        this.setMesh()
    }

    setGeometry() {
        this.geometry = new THREE.PlaneGeometry(2, 2, 1, 1)
    }

    setColor() {
        this.color = {}
        this.color.value = '#05072B'
        this.color.instance = new THREE.Color(this.color.value)
    }

    setMaterial() {
        this.material = new THREE.ShaderMaterial({
            depthWrite: false,
            depthTest: false,
            transparent: true,
            uniforms: {
                uColor: { value: this.color.instance },
                uOffset: { value: -0.2 },
                uMultiplier: { value: 3.0 }
            },
            vertexShader: vertexShader,
            fragmentShader: fragmentShader
        })
    }

    setMesh() {
        this.mesh = new THREE.Mesh(this.geometry, this.material)
        this.mesh.scale.x = 20
        this.mesh.scale.y = 10
        this.mesh.position.z = 1
        this.scene.add(this.mesh)
    }

    update() {
    }
}
