import * as THREE from 'three'
import Experience from './Experience.js'

export default class Smoke {
    constructor() {
        this.experience = new Experience()
        this.resources = this.experience.resources
        this.scene = this.experience.scene
        this.time = this.experience.time
        this.world = this.experience.world

        this.count = 6
        this.group = new THREE.Group()
        this.group.position.y = -2
        this.scene.add(this.group)

        this.setGeometry()
        this.setMaterial()
        this.setItems()
    }

    setGeometry() {
        this.geometry = new THREE.PlaneGeometry(1, 1, 1, 1)
    }

    setMaterial() {
        this.material = new THREE.MeshBasicMaterial({
            depthWrite: false,
            transparent: true,
            alphaMap: this.resources.items.smokeTexture,
            opacity: 0.01 + Math.random() * 0.2
        })
    }

    setItems() {
        this.items = []

        for (let i = 0; i < this.count; i++) {

            const item = {}

            item.elevationSpeed = Math.random() * 0.0005
            item.rotationSpeed = (Math.random() - 0.5) * Math.random() * 0.0005

            const scale = 3 + Math.random() * 3

            item.mesh = new THREE.Mesh(this.geometry, this.material)
            item.mesh.scale.set(scale, scale, scale)
            item.mesh.position.x = (Math.random() - 0.5) * 6
            this.group.add(item.mesh)

            this.items.push(item)
        }
    }

    resize() { }

    update() {
        for (const item of this.items) {
            item.mesh.rotation.z = this.time.elapsed * item.rotationSpeed
            item.mesh.position.y = Math.sin(this.time.elapsed * item.elevationSpeed)
        }
    }
}
