import * as THREE from 'three'
import Experience from './Experience.js'

export default class Triangle {
    constructor() {
        this.experience = new Experience()
        this.scene = this.experience.scene
        this.sizes = this.experience.sizes
        this.time = this.experience.time
        this.resources = this.experience.resources
        this.world = this.experience.world
        this.mouse = this.experience.mouse

        this.setGeometry()
        this.setTextures()
        this.setMaterial()
        this.setMesh()
        this.setScroll()
    }

    setGeometry() {
        this.geometry = new THREE.PlaneGeometry(1, 1, 1, 1)
    }

    setTextures() {
        this.textures = {}
        this.textures.color = this.resources.items.triangleTexture
        this.textures.encoding = THREE.sRGBEncoding
    }

    setMaterial() {
        this.material = new THREE.MeshStandardMaterial({
            depthWrite: false,
            transparent: true,
            map: this.textures.color
        })
    }

    setMesh() {
        this.mesh = new THREE.Mesh(this.geometry, this.material)
        this.scene.add(this.mesh)
    }

    setScroll() {
        this.mouse.scroll = window.scrollY

        window.addEventListener('scroll', () => {
            this.mouse.scroll = window.scrollY
        })
    }

    update() {
        this.mesh.position.z = 1.2 + Math.sin(this.time.elapsed * 0.0005) * 0.1
        this.mesh.rotation.y = Math.sin(this.time.elapsed * 0.001) * 0.1
        this.mesh.position.x = 0.8 + (-this.mouse.scroll / this.sizes.height * 1)
        this.mesh.rotation.z = -0.2 + (this.mouse.scroll / this.sizes.height * 0.2)
    }

}